.sf_card {
  width: 100%;
  background-color: #fff;
  border: 0.5px solid #cacaca;
  border-radius: 6px;
  color: #1c023b;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  margin-top: 0.5vw;
  padding: 1vw;
  position: relative;
}
.sf_card_contatiner {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 1rem;
}
.sf_card_contatiner > img {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
}

.sf_card_contatiner_inner > h6 {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #121212;
  margin-bottom: 0.2rem;
}
.sf_card_contatiner_inner > p {
  font-weight: 400;
  font-size: 0.878vw;
  line-height: 1.098vw;
  color: rgba(0, 0, 0, 0.6);
  margin-bottom: 0;
}
